import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { API_KEY, API_URL, IMAGE_BASE_URL } from '../Config';
import MainImage from '../LandingPage/Section/MainImage';
import MovieInfo from './MovieInfo';
import { Button } from 'antd';
import { Row } from 'antd';
import { useNavigate } from 'react-router-dom';
import AntCard from '../commons/AntCard';


function Detail() {
  const navigate = useNavigate()
  const { movieId } = useParams();
  // console.log('movieId >> ', movieId);

  // [state] ======================================
  const [Movie, setMovie] = useState({});
  const [Casts, setCasts] = useState([]);

  const [CrewToggle, setCrewToggle] = useState(false);
  const [ActorToggle, setActorToggle] = useState(false);

  useEffect(() => {
    // console.log(' , .');

    //// [   URL]
    // let endpointInfo = `https://api.themoviedb.org/3/movie/${movieId}`;
    let endpointInfo = `${API_URL}${movieId}?api_key=${API_KEY}`;
    // console.log(endpointInfo);

    //// [  ] URL
    // https://api.themoviedb.org/3/movie/movie_id/credits?api_key=
    let endpointCrew = `${API_URL}${movieId}/credits?api_key=${API_KEY}`;
    console.log('', endpointCrew);

    //// [  ]    
    fetch(endpointInfo)
      .then(response => response.json())
      .then(response => {
        // console.log(response)
        setMovie(response)
      });

    //// [   ]
    fetch(endpointCrew)
      .then(response => response.json())
      .then(obj => {
        console.log(obj)
        setCasts(obj.cast)
      });

    //// [ ]

  }, []);

  //   == =====================================
  function toggleActorView() {
    // console.log(' !!!')
    setActorToggle(!ActorToggle)

    // console.log('ActorToggle  >> ', ActorToggle)
  }

  function toggleCrewView() {

    setCrewToggle(!CrewToggle)
  }




  return (
    // <div>Detail</div>
    <>
      {/* Header */}
      {Movie &&
        <MainImage
          image={`${IMAGE_BASE_URL}w1280${Movie.poster_path}`}
          title={Movie.title}
          overview={Movie.overview}
        />
      }

      {/* Body */}
      <div style={{ width: '85%', margin: ' 20px auto ' }}>


        {/*  */}
        <div style={{ textAlign: 'center', marginTop: '60px 0' }}>
          <Button onClick={() => navigate(-1)}> </Button>
        </div>
        {/* navigate(-1) =   */}



        {/* Movie Info */}
        <MovieInfo movie={Movie} />

        <br />

        {/* Actor Grid */}
        <div style={{ display: 'flex', justifyContent: 'center',  margin: '20px 0' }}>
          <Button onClick={toggleActorView}> </Button>
          <Button style={{ marginLeft: '15px' }}Button onClick={toggleCrewView}> </Button>
        </div>

        {ActorToggle &&
          <Row gutter={[10, 10]}>
            {Casts.map(cast => {
              return (
                <React.Fragment key={cast.id} >
                  {cast.profile_path &&
                    <AntCard
                      path={`${IMAGE_BASE_URL}w400${cast.profile_path}`}
                      castName={cast.name}
                    />
                  }
                </React.Fragment>
              );
            })}
          </Row>
        }
      </div>
    </>
  )
}

export default Detail